import React, {useState} from 'react';
import s from './Register.module.scss'
import Button from "../common/button/Button";
import {withRouter} from "react-router-dom";
import {connect} from 'react-redux'
import {compose} from 'redux'
import {loginUser, resetPassword} from '../../redux/reducers/auth'
import { useHistory } from "react-router-dom";
import Modal from "../common/Modal/Modal";

const Login = ({
   onToggle,
   isFromExtension=false,
   isLoading,
   loginUser,
   onClose,
   error,
   resetPassword
}) => {
    const [username, setUsername] = useState('')
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const [openForgotPassword, setOpenForgotPassword] = useState(false)
    const history = useHistory()

    const onSubmit = (e) => {
        console.log('CLICK')
        e.preventDefault()
        loginUser({
            username, password
        }).then((res) => {
            if(res.ok){
                if(!isFromExtension){
                    onClose()
                    history.push('/dashboard')
                }
            }
        })
    }

    const forgotPassHandler = (e) => {
        e.preventDefault()
        resetPassword({ email })
            .then((res) => {
                console.log('Res', res);
                res.ok && setOpenForgotPassword(false)
            })
            .catch((error) => {
                console.log(error)
            })
    }

    return (
        <div className={s.registerWrapper}>
           <h2>Login</h2>
           <form className={s.formContainer} onSubmit={onSubmit}>
                <div className={s.formInput}>
                    <input
                        type={'text'}
                        required
                        name={'username'}
                        placeholder={'Username'}
                        onChange={(e) => setUsername(e.target.value)}
                    />
                </div>
                <div className={s.formInput}>
                    <input
                        type={'password'}
                        required
                        name={'password'}
                        placeholder={'Password'}
                        onChange={(e) => setPassword(e.target.value)}
                    />
                </div>
                <div className={s.btnContainer}>
                    <Button
                        label={'Login'}
                        type={'submit'}
                        isLoading={isLoading}
                    />
                </div>
               {
                   !isFromExtension ?
                      <div>
                          <p>
                              Don't have an account? <span onClick={onToggle}>Sign Up</span>
                          </p>
                          <p>
                              <span onClick={() => setOpenForgotPassword(true)}>Forgot Password</span>
                          </p>
                      </div> : null
               }
            </form>
            <Modal openModal={openForgotPassword} onClose={() => setOpenForgotPassword(false)}>
                <form className={s.formContainer} onSubmit={forgotPassHandler}>
                    <h2 style={{textAlign: 'center'}}>Password Reset</h2>
                    <div className={s.formInput}>
                        <input
                            type={'email'}
                            required
                            name={'email'}
                            placeholder={'email'}
                            onChange={(e) => setEmail(e.target.value)}
                        />
                    </div>
                    <div className={s.btnContainer}>
                        <Button
                            label={'Submit'}
                            type={'submit'}
                        />
                    </div>
                </form>
            </Modal>
        </div>
    );
};

const mapStateToProps = (state) => ({
    isAuthenticated: state.auth.isAuthenticated,
    isLoading: state.loading.isLoading,
    error: state.errors,
    slug: state.auth.slug
})

export default compose(
    connect(mapStateToProps, {loginUser, resetPassword}),
    withRouter
)(Login)
